import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { TournamentDetail } from '../tournamentDeatil';
import { HttpServiceService } from '../http-service.service';

@Component({
  selector: 'app-tournament-detail',
  templateUrl: './tournament-detail.component.html',
  styleUrls: ['./tournament-detail.component.css']
})
export class TournamentDetailComponent implements OnInit {

  @Input() tournamentDetail: TournamentDetail;
  constructor(
    private route: ActivatedRoute,
    private httpService: HttpServiceService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getTournamentDetail();
  }

  getTournamentDetail(): void {
    const id = +this.route.snapshot.paramMap.get('tournamentId');
    this.httpService.getTournament(id)
      .subscribe(tournament => this.tournamentDetail = tournament);
  }

  goBack(): void {
    this.location.back();
  }

}
