import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { BookOnlineComponent } from './book-online/book-online.component';
import { TournamentDetailComponent } from './tournament-detail/tournament-detail.component';
import { LatestNewsComponent } from './latest-news/latest-news.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { ContactComponent } from './contact/contact.component';


const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'book', component: BookOnlineComponent },
  { path: 'book/:tournamentId', component: TournamentDetailComponent },
  { path: 'news', component: LatestNewsComponent },
  { path: 'about', component: AboutUsComponent },
  { path: 'contact', component: ContactComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
