import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { Notification } from './notification';
import { Tournament } from './tournament';
import { TournamentDetail } from './tournamentDeatil';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class HttpServiceService {
  private apiUrlNotification = 'api/notifications';  // URL to web api
  private apiUrlTournament = 'api/tournaments';
  private apiUrlTournamentDetail = 'api/tournamentDetail'; // URL to web api

  constructor(private http: HttpClient) { }

  /** GET Notifications from the server */
  getNotifications(): Observable<Notification[]> {
    return this.http.get<Notification[]>(this.apiUrlNotification)
      .pipe(
        catchError(this.handleError('getNotifications', []))
      );
  }

  /** GET Tournaments from the server */
  getTournaments(): Observable<Tournament[]> {
    return this.http.get<Tournament[]>(this.apiUrlTournament)
      .pipe(
        catchError(this.handleError('getTournaments', []))
      );
  }

  /** GET Tournament by id */
  getTournament(id: number): Observable<TournamentDetail> {
    // const url = `${this.apiUrlTournament}/${id}`;
    return this.http.get<TournamentDetail>(this.apiUrlTournamentDetail).pipe(
      catchError(this.handleError<TournamentDetail>(`getTournament id=${id}`))
    );
  }

  /**
 * Handle Http operation that failed.
 * Let the app continue.
 * @param operation - name of the operation that failed
 * @param result - optional value to return as the observable result
 */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
