import { Component, OnInit } from '@angular/core';

import { Tournament } from '../tournament';
import { HttpServiceService } from '../http-service.service';

@Component({
  selector: 'app-book-online',
  templateUrl: './book-online.component.html',
  styleUrls: ['./book-online.component.css']
})
export class BookOnlineComponent implements OnInit {

  constructor(private httpService: HttpServiceService) { }
  allTournaments: Tournament[] = [];

  ngOnInit() {
    this.getListOfTournaments();
  }

  getListOfTournaments(): void {
    this.httpService.getTournaments().subscribe(tournaments => this.allTournaments = tournaments);
  }

}
