import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'ValiumHub';
  mangeNavBar(): void {
    const element1 = document.getElementById('myTopnav');
    const element2 = document.getElementById('icon');
    element2.classList.toggle('change');
    if (element1.className === 'topnav') {
      element1.className += ' responsive';
    } else {
      element1.className = 'topnav';
    }
  }
}
